import React from "react";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";

function Menu(props) {

    return (
        <div className="scrollmenu" >
          <Link to="/video/home" onClick={() => props.setCurTab('home')} style={{backgroundColor: props.curTab === 'home' ? 'red' : null}}>首頁</Link>
          <Link to="/video/collect" onClick={() => props.setCurTab('collect')} style={{backgroundColor: props.curTab === 'collect' ? 'red' : null}}>收藏頁</Link>
          <Link to="/video/play" onClick={() => props.setCurTab('play')} style={{backgroundColor: props.curTab === 'play' ? 'red' : null}}>播放頁</Link>
        </div>
    )
}

export default Menu;